import React, { Component } from 'react';
import { deleteList, fetchLists } from '../actions/listActions';
import { fetchItems } from '../actions/itemActions';
import { connect } from 'react-redux';
import { LISTS_URL, ITEMS_URL } from '../listrUrls';
import PropTypes from 'prop-types';
import ListDetail from './ListDetail';
import "../css/ListStyle.css";

class Lists extends Component {

  constructor(props){
    super(props);
    this.state = {
      currentList: {},
      modalOpen: false,
    }
    this.showItems = this.showItems.bind(this);
    this.toggleDetails = this.toggleDetails.bind(this);
  }

  showItems(e, data, listType){
    e.preventDefault();
    this.setState({currentList: data});
    this.props.fetchItems(ITEMS_URL, data.static_id, this.props.token);
    if(!this.state.modalOpen){
      this.toggleDetails();
    }
  }

  toggleDetails(){
    this.setState((prevState) => ({modalOpen: !prevState.modalOpen}));
  }

  render () {
    var hideRemove;
    var detailModal;
    var userCount;

    const cardStyle = {
      display: 'inline-block',
      marginTop: '10px',
      marginRight: '10px',
      minWidth: '250px'
    }
    var titleStyle = {
      display: 'inline-block',
      marginRight: '10px',
    }
    detailModal = <ListDetail isOpen={this.state.modalOpen} toggle={this.toggleDetails} data={this.state.currentList} listType={this.props.listType}/>
    const data = this.props.data.map(list => (
      <a href="" onClick={(e) => this.showItems(e, list)} class="custom-card">
        <div class="hover-card card bg-white" style={cardStyle}>
          <div class="card-body">
            <h4 class="card-title">{list.name}</h4>
            <h6 class="card-subtitle mb-2 text-muted">{list.owner}</h6>
          </div>
        </div>
      </a>
    ));
    return(
      <div>
        {data}
        {detailModal}
      </div>
    );
  }
}

Lists.proptypes = {
  deleteList: PropTypes.func.isRequired,
  fetchLists: PropTypes.func.isRequired,
  fetchItems: PropTypes.func.isRequired,
}

const mapStateToProps = state => ({
  token: state.users.token,
  items: state.items.items,
  itemsLoading: state.items.loading
});

export default connect(mapStateToProps, { deleteList, fetchLists, fetchItems })(Lists);



